import axios from "axios";

const baseURL =
  process.env.NODE_ENV === "production"
    ? "https://tedxauckland-profiles.herokuapp.com//api/v1"
    : "http://localhost:3000/api/v1";

const apiClient = axios.create({
  baseURL: baseURL,
  withCredentials: false,
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json"
  }
});

export default {
  getEvent(slug) {
    return apiClient.get(`/events/${slug}`);
  },

  getRSVP(eventSlug, personId) {
    return apiClient.get(`/events/${eventSlug}/rsvp/${personId}`);
  },
  getNewRSVP(eventSlug) {
    return apiClient.get(`/events/${eventSlug}/rsvp/new`);
  },
  createRSVP(eventSlug, data) {
    return apiClient.post(`/events/${eventSlug}/rsvp/`, data);
  },
  updateRSVP(eventSlug, personId, data) {
    return apiClient.patch(`/events/${eventSlug}/rsvp/${personId}`, data);
  },

  submitApplication(eventSlug, data) {
    return apiClient.post(`/events/${eventSlug}/ticket_applications/`, data);
  },
  getApplication(eventSlug, id) {
    return apiClient.get(`/events/${eventSlug}/ticket_applications/${id}`);
  },

  getTicketOffer(eventSlug, id) {
    return apiClient.get(`/events/${eventSlug}/ticket_offers/${id}`);
  },
  getNewTicketRecipient(eventSlug) {
    return apiClient.get(`/events/${eventSlug}/purchase_for_others/new`);
  },

  acceptTicket(eventSlug, data) {
    return apiClient.post(
      `/events/${eventSlug}/ticket_offer_acceptances/`,
      data
    );
  },
  rejectTicket(eventSlug, data) {
    return apiClient.post(
      `/events/${eventSlug}/ticket_offer_rejections/`,
      data
    );
  },

  updatePersonInfo(id, data) {
    return apiClient.patch(`/ticket_offers/${id}`, data);
  }
};
